import React from 'react'
import ReactDOM from 'react-dom'
import { useSpring, animated } from 'react-spring'
import './styles.css'
import { Parallax, ParallaxLayer } from '@react-spring/parallax' // // Icons made by Freepik from www.flaticon.com

function Card() {
  const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 10, tension: 550, friction: 140 } }))
  return (
    <Parallax pages={2} style={{ top: '0', left: '0' }}>
      <ParallaxLayer
        offset={0}
        speed={2.5}
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <p>Scroll down</p>
      </ParallaxLayer>

      <ParallaxLayer offset={1} speed={2} style={{ backgroundColor: 'green' }} />

      <ParallaxLayer
        offset={1}
        speed={0.5}
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          color: 'white',
        }}>
        <p>Scroll up</p>
      </ParallaxLayer>
    </Parallax>
  )
}

ReactDOM.render(<Card />, document.getElementById('root'))
